@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,600,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Import font from https://fonts.google.com/specimen/Nunito+Sans */

:root {
    --very-light-gray: hsl(0, 0%, 98%);
}

body {
    background-color: hsl(0, 0%, 98%);
    background-color: var(--very-light-gray);
    font-family: 'Nunito Sans', sans-serif;
}

header {
    display: flex;
    justify-content: space-between;
    padding: 0 4%;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: hsl(0, 0%, 98%);
    background-color: var(--very-light-gray);
}

.mode-toggle {
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 30px;
    margin: auto 0;
}

.mode-toggle img {
    padding: 5px;
}

nav {
    display: flex;
    justify-content: space-between;
    padding: 50px 4%;
}

.search {
    display: flex;
    background-color: white;
    border-radius: 5px;
    width: 400px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    align-items: center;
}

.search img {
    height: 20px;
    padding-left: 20px;
}

input {
    background-color: transparent;
    border: none;
    padding: 15px;
    width: 100%;
}

input:focus-visible, select:focus-visible {
    outline: none;
}

select {
    background-color: white;
    border: none;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    width: 175px;
    padding: 0 10px;
    border-right: 5px solid transparent;
    cursor: pointer;
}

select * {
    padding: 10px;
}

main {
    padding: 0 4%;
}

.cards-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-gap: 80px 40px;
    gap: 80px 40px;
}

.country-card {
    background-color: white;
    width: 300px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 400px;
}

.country-card:hover {
    box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.4);
}

.country-card img {
    width: 100%;
    max-height: 200px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.country-card-info {
    padding: 0px 20px 10px;
}

.country-card-info p {
    margin: 5px;
}

strong {
    font-weight: 600;
}

button {
    display: flex;
    height: 30px;
    width: 100px;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: none;
    outline: none;
    box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

button:hover {
    box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.4);
}

button img {
    height: 15px;
    padding-right: 5px;
}

.info-page {
    display: flex;
    justify-content: space-between;
}

.flag {
    width: 45%;
}

.flag img {
    width: 100%;
    max-height: 500px;
}

.info {
    width: 45%;
}

.info-details {
    display: flex;
    justify-content: space-between;
}

.border-info {
    margin: 50px 0;
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
}
@media (max-width: 700px) {

    header h1 {
        font-size: 23px;
    }

    .cards-area {
        flex-direction: column;
        align-items: center;
        grid-gap: 40px 0;
        gap: 40px 0;
    }

    nav {
        flex-direction: column;
        padding: 50px 4% 20px;
    }

    .search {
        width: 100%;
    }

    select {
        margin: 40px 0;
        height: 40px;
    }

    .info-page {
        flex-direction: column;
    }

    .flag {
        width: 100%;
        margin-top: 40px;
    }

    .info {
        width: 100%;
    }

    .info-details {
        flex-direction: column;
        grid-gap: 40px 0;
        gap: 40px 0;
    }

    .border-info span {
        width: 100%;
    }

    .border-info {
        justify-content: left;
    }
}
